(function () {
    'use strict';
    var angular = require('angular');
    require('./cancel-application.component.scss');

    var cancelApplication = angular.module('app').component('cancelApplication', {
        template: require('./cancel-application.component.html'),
        controller: ['$anchorScroll', 'appState', 'analyticService', function ($anchorScroll, appState, analyticService) {
            var vm = this;
            vm.$onInit = function () {
                vm.isStaffJourney = appState.isStaffJourney();
                analyticService.trackPageLoad(analyticService.pageList.dipCancelApplication);
                appState.clearAll();
                $anchorScroll();
            };
            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = cancelApplication.name;
})();



